import {
	getJobApply,
} from '@/api/job'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
export default {
	data() {
		return {
			ready: false,
			active: 0,
			tabs: 0,
			all: {
				list: null,
				count: null,
				search: {
					page: 1,
					limit: 5
				}
			},
			cy: {
				list: null,
				count: null,
				search: {
					state: 2,
					page: 1,
					limit: 5
				}
			},
			xq: {
				list: null,
				count: null,
				search: {
					state: 3,
					page: 1,
					limit: 5
				}
			},
			ms: {
				list: null,
				count: null,
				search: {
					state: 4,
					page: 1,
					limit: 5
				}
			},
		}
	},
	onReady() {
		this.ready = true
	},
	onLoad(e) {
		if (e.active) {
			this.active = e.active
			this.tabs = e.active
		}
		this.getJobApply()
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	methods: {
		getJobApply() {
			let t
			const s = parseFloat(this.active)
			if (s === 0) {
				t = this.all
			} else if (s === 1) {
				t = this.cy
			} else if (s === 2) {
				t = this.xq
			} else if (s === 3) {
				t = this.ms
			}
			getJobApply(t.search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.count = r.list.count
					if (t.list && t.list.length > 0) {
						t.list = t.list.concat(r.list.rows)
					} else {
						t.list = r.list.rows
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
					setTimeout(() => {
						uni.navigateBack();
					}, 1500)
				}
			})
		},
		refresher() {
			let t
			const s = parseFloat(this.active)
			if (s === 0) {
				t = this.all
			} else if (s === 1) {
				t = this.cy
			} else if (s === 2) {
				t = this.xq
			} else if (s === 3) {
				t = this.ms
			}
			if (t.count > t.list.length) {
				t.search.page = t.search.page++
				this.getJobApply()
			} else {
				$wuxToast().hide()
				$wuxToast().show({
					type: 'forbidden',
					text: '已加载全部'
				})
			}
		},
		onClick(v) {
			let t
			const e = v.detail.name
			this.active = e
			if (e === 0) {
				t = this.all
			} else if (e === 1) {
				t = this.cy
			} else if (e === 2) {
				t = this.xq
			} else if (e === 3) {
				t = this.ms
			}
			if (!t.count && !t.list) {
				this.getJobApply()
			} else if (t.count > t.list.length) {
				t.search.page = t.search.page++
				this.getJobApply()
			}
		},
		jobTo() {
			uni.switchTab({
				url: '/pages/job/index'
			});
		},
		detailTo(id) {
			uni.navigateTo({
				url: '/pages/job/detail?id=' + id
			});
		},
		inviteTo(id) {
			uni.navigateTo({
				url: '/pages/job/invite?id=' + id
			});
		}
	}
}
